.link-area{
    border: 2px solid #ffba00;
    -webkit-transition: width 1s,height 1s,top 1s,left 1s,background-color 0.2s;
    -o-transition: width 1s,height 1s,top 1s,left 1s,background-color 0.2s;
    transition: width 1s,height 1s,top 1s,left 1s,background-color 0.2s;
    cursor: pointer;
}

.link-area:hover{
    background-color: rgba(255, 231, 113, 0.33);
}


